.home-page {
  text-align: center;
  padding: 50px;
}

.start-button {
  display: inline-block;
  padding: 15px 30px;
  margin-top: 20px;
  background-color: #007bff;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.start-button:hover {
  background-color: #0056b3;
}

.template-selection {
  text-align: center;
  padding: 20px;
}

.template-grid {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.template-card {
  cursor: pointer;
  border: 1px solid #ccc;
  padding: 10px;
  width: 200px;
  transition: transform 0.2s;
}

.template-card:hover {
  transform: scale(1.05);
}

.template-card img {
  width: 100%;
  height: auto;
}
.resume-builder {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.resume-builder h1 {
  text-align: center;
}

.resume-builder form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 30px;
}

.resume-builder label {
  display: flex;
  flex-direction: column;
  font-weight: bold;
}

.resume-builder input,
.resume-builder textarea {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.resume-builder button {
  padding: 10px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.resume-builder button:hover {
  background-color: #0056b3;
}

.resume-preview {
  margin-top: 40px;
}

.preview-content {
  border: 1px solid #ccc;
  padding: 20px;
  border-radius: 10px;
  background-color: #f9f9f9;
}

.preview-content h3,
.preview-content h4 {
  margin-top: 0;
}
